.site {
	max-width: 1220px;
	min-width: 320px;

	@include mq(1220) {
		margin: 0 auto;
	}
}

section,
.post {

	.csstransitions & {
		opacity: 0;
		transform: translate3d(-20px, 0, 0);
		transition: transform 1s, opacity 2s, margin 300ms;

		@include mq($bp-tablet) {
			transform: translate3d(-50px, 0, 0);
		}

		@for $i from 1 through 15 {
			&:nth-child(#{$i}) {
				transition-delay: .8s + (.2 * $i);
			}
		}
	}

	.go & {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}
}

a {
	color: $color-accent;
	text-decoration: none;
	transition: color .2s ease-in-out;

	&:hover {
		color: $color-hover;
	}
}

h2 {
	@include font-size(18);
	color: $color-title;
	display: block;
	font-family: $font-title;
	line-height: 1;
	margin-bottom: 30px;
	position: relative;
	text-transform: uppercase;

	@include mq($bp-tablet, true) {
		@include font-size(28);
		margin-bottom: 40px;
	}

	a {
		color: $color-title;
		text-decoration: none;

		&:hover {
			color: $color-accent;
		}
	}

	span {
		display: none;
		position: absolute;
		right: 20px;
		top: 0;
	}
}
